import { Model } from './base/Model';
export class CardsData extends Model {
    set cards(cards) {
        this._cards = cards;
        this._cards.forEach((card) => (card.selected = false));
    }
    get cards() {
        return this._cards;
    }
    setPreview(card) {
        this._preview = card.id;
        this.events.emit('preview:changed', card);
    }
    getCard(cardId) {
        const card = this._cards.find((card) => card.id === cardId);
        if (card) {
            return card;
        }
        else {
            return undefined;
        }
    }
    toggleSelected(card) {
        if (!card.selected) {
            card.selected = true;
            this.events.emit('preview:changed', card);
        }
        else {
            card.selected = false;
        }
        this.events.emit('basket:changed');
    }
    getAddedProducts() {
        return this._cards.filter((card) => card.selected);
    }
    getTotalPrice() {
        return this.getAddedProducts().reduce((total, card) => total + card.price, 0);
    }
    resetSelected() {
        this._cards.forEach((card) => (card.selected = false));
        this.events.emit('basket:changed');
    }
}
